import React from 'react';
import {
  NavLink
} from "react-router-dom";


const Navigator = () =>{

  return(
    <div className="navigator">
      <div className="navigator__brand">
        <h1>Jorzon</h1>
      </div>
      <div className="navigator__nav">

          <NavLink to="/work"  >
            Work
          </NavLink>

          <NavLink to="/about" activeClassName="active" >
           About
          </NavLink>

          <NavLink to="/contact"  >
            Contact
          </NavLink>
     
      </div>
    </div>
 
  );
}

export default Navigator